.container {
    width: 100%;
    height: 100vh;
    background: rgb(64,83,94);
    background: radial-gradient(circle, rgba(64,83,94,1) 0%, rgba(8,20,45,1) 100%);
}

.topbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 10px 25px;
}

.info-area {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex: 1;
}

.info-area a {
    font: 500 16px sans-serif;
    color: #ffffff;
}

.topbar .logo img{
    width: 120px;
}